import axios from "axios";
import { useEffect, useMemo, useState, useRef } from "react";

import update from 'immutability-helper';

import { useNavigate } from 'react-router-dom';


function Base() {

    //          select   !!!
    //  !

    //         csrf-

    // npm run build 
    
    axios.defaults.xsrfCookieName = 'csrftoken';
    axios.defaults.xsrfHeaderName = 'X-CSRFToken';

    const [simcardData, setSimcardData] = useState(null);
    const [tariffData, setTariffData] = useState(null);
    const [newRecordTariff, setNewRecordTariff] = useState(1);

    const [deletableSimcardId, setDeletableSimcardId] = useState(null);

    const closeDeleteModal = useRef(null);

    const [updateRecordTariff, setUpdateRecordTariff] = useState(1);

    //     id    backend.  IMEI           

    const [updatableSimcardId, setUpdatableSimcardId] = useState(null);

    //     simcardData ( )

    const [updatableSimcardIndex, setUpdatableSimcardIndex] = useState(null);

    //     backupSimcardData ( )

    const [updatableBackupSimcardIndex, setUpdatableBackupSimcardIndex] = useState(null);


    //     -  

    const [username, setUsername] = useState(null);

    const navigate = useNavigate();

    //   

    //    (     ) 

    //  

    // const [searchName, setSearchName] = useState("");

    const [backupSimcardData, setBackupSimcardData] = useState("");

    //    

    const clientNameSearchField = useRef(null);

    useMemo(() => {

      //      /check-user/,   check-user/
      //       login/check-user/
      
      axios.get("/check-user/", 

        {
            headers: {
            'Content-Type': 'application/json',
            }

        }
        ).then(response => {
            if(response.data.user.username === 'AnonymousUser'){
                //          
                //   
                
                navigate('/login/');
            }

            setUsername(response.data.user.username);

        }).catch((error) => {
        console.log("error", error)

        alert(': ' + error);

        });


        // http://localhost:8000       

        axios.get("/get-data/").then(final_data => {

        setTariffData(final_data.data.tariffs);     
        
        setSimcardData(final_data.data.simcards);
          
        //        

        setBackupSimcardData(final_data.data.simcards);
        
        setNewRecordTariff(final_data.data.tariffs[0]);

      });    
      
    }, [navigate]);
    

    useEffect(() => {

      //            useEffect


        // const GETDATA = async () => {
        //   await ...
        // }



    }, []);

    //     

    const handleLogout = (event) => {
      event.preventDefault();

      axios.post("/logout-user/", 
        {
          'username': username
        },
        {
          headers: {
          'Content-Type': 'application/json',
        }
        }
      ).then(response => {

        navigate('/login/');

      }).catch((error) => {
        console.log("error", error.response.data.error_message);

        alert(': ' + error.response.data.error_message);

        });

    }



    //       

    const handleDropoutFormData = () => {

      //   
      document.getElementById("IMEI_name").value = null;
      document.getElementById("Phone_name").value = null;
      document.getElementById("CustomerName_name").value = null;
      document.getElementById("RegDate_name").value = null;
      document.getElementById("ServiceType_name").value = 1;


    }

    //       

    const handleUpdateDropoutFormData = () => {

      //  Id    (    ,         )

      setUpdatableSimcardId(null);

      document.getElementById("IMEI_update_name").value = null;
      document.getElementById("Phone_update_name").value = null;
      document.getElementById("CustomerName_update_name").value = null;
      document.getElementById("RegDate_update_name").value = null;
      document.getElementById("ServiceType_update_name").value = 1;

    }

    // -   

    const handleSubmit = (event) => {
      event.preventDefault();

      axios.post("/new-simcard/", 
        {
          'IMEI': event.target.elements.IMEI_name.value,
          'PhoneNumber': event.target.elements.Phone_name.value,
          'ClientName': event.target.elements.CustomerName_name.value,
          'RegistrationDate': event.target.elements.RegDate_name.value,
          'TariffPlan': event.target.elements.ServiceType_name.value,
        },
        {
          headers: {
          'Content-Type': 'application/json',
        }
        }
      ).then(response => {

            alert(response.data.Message);

            //       

            document.getElementById('closeNewRecordModal_btn').click();

            //   
            event.target.elements.IMEI_name.value = null;
            event.target.elements.Phone_name.value = null;
            event.target.elements.CustomerName_name.value = null;
            event.target.elements.RegDate_name.value = null;
            event.target.elements.ServiceType_name.value = 1;

            //     

            const newSimcard = response.data.Simcard;

            //      

            // https://stackoverflow.com/questions/54676966/push-method-in-react-hooks-usestate

            //   
            setBackupSimcardData(oldArray => [...oldArray, newSimcard])

            //     
            setSimcardData([...backupSimcardData, newSimcard]);

            //     
            clientNameSearchField.current.value = '';


            //      ,      
            //          

            //  
            // const modal = document.getElementById('createNewRecordModal');

            //  
            // modal.classList.remove('show');
            // modal.setAttribute('aria-hidden', 'true');
            // modal.setAttribute('style', 'display: none');

            //   
            // const modalBackdrops = document.getElementsByClassName('modal-backdrop');

            //   
            // document.body.removeChild(modalBackdrops[0]);


    }).catch((err) => {alert(err)});

    
    }



    // -   

    const handleSimcardUpdate = (event) => {
      event.preventDefault();

    const newArray = update(simcardData, { [updatableSimcardIndex]: {$merge: {
      'IMEI': event.target.elements.IMEI_update_name.value,
      'PhoneNumber': event.target.elements.Phone_update_name.value,
      'ClientName': event.target.elements.CustomerName_update_name.value,
      'RegistrationDate': event.target.elements.RegDate_update_name.value,
      TariffPlan: {
      'Title': tariffData.filter(tariff => tariff.id === parseInt(event.target.elements.ServiceType_update_name.value, 10))[0].Title,
      }
    }} });

    //     
    const backupArray = update(backupSimcardData, { [updatableBackupSimcardIndex]: {$merge: {
      'IMEI': event.target.elements.IMEI_update_name.value,
      'PhoneNumber': event.target.elements.Phone_update_name.value,
      'ClientName': event.target.elements.CustomerName_update_name.value,
      'RegistrationDate': event.target.elements.RegDate_update_name.value,
      TariffPlan: {
      'Title': tariffData.filter(tariff => tariff.id === parseInt(event.target.elements.ServiceType_update_name.value, 10))[0].Title,
      }
    }} });
    
    //           

    // alert(tariffData.filter(tariff => tariff.id === parseInt(event.target.elements.ServiceType_update_name.value, 10))[0].Title);

    // alert(simcardData[0].TariffPlan.Title);

    setSimcardData(newArray);

    setBackupSimcardData(backupArray);

    

    //       


      axios.post("/update-simcard/", 
        {
          'id': updatableSimcardId,
          'IMEI': event.target.elements.IMEI_update_name.value,
          'PhoneNumber': event.target.elements.Phone_update_name.value,
          'ClientName': event.target.elements.CustomerName_update_name.value,
          'RegistrationDate': event.target.elements.RegDate_update_name.value,

          //  id ,         
          'TariffPlan': event.target.elements.ServiceType_update_name.value,
        },
        {
          headers: {
          'Content-Type': 'application/json',
        }
        }
      ).then(response => {

            alert(response.data.Message);

            //       

            document.getElementById('closeUpdateRecordModal_btn').click();

            setUpdatableSimcardId(null);

            //   
            event.target.elements.IMEI_update_name.value = null;
            event.target.elements.Phone_update_name.value = null;
            event.target.elements.CustomerName_update_name.value = null;
            event.target.elements.RegDate_update_name.value = null;
            event.target.elements.ServiceType_update_name.value = 1;

            //     

            // const newSimcard = response.data.Simcard;

            //      

            // https://stackoverflow.com/questions/54676966/push-method-in-react-hooks-usestate

            // setSimcardData(oldArray => [...oldArray, newSimcard]);



    }).catch((err) => {
      
      //         'error_message'   ,  ... 

      if(Object.keys(err.response.data).length !== 0 && "error_message" in err.response.data) {

          alert('Error: ' + err.response.data.error_message);

      } else {

        alert(err);

      }
    
    });

    
  }






    // -  

    const handleSimcardDelete = (event) => {

      //       

      event.preventDefault();

      //        backend-

      axios.post("/delete-simcard/", 
        {
          'id': deletableSimcardId,
        },
        {
          headers: {
          'Content-Type': 'application/json',
        }
        }
      ).then(response => {}).catch((err) => {alert(err)});

      //  useRef-          

      // https://stackoverflow.com/questions/39913863/how-to-manually-trigger-click-event-in-reactjs

      closeDeleteModal.current?.click();

      //       

      setSimcardData(simcardData.filter(record => record.id !== deletableSimcardId));

      //   

      setBackupSimcardData(backupSimcardData.filter(record => record.id !== deletableSimcardId));




    }

    return (<div>

    <h1 className="mx-2">    </h1>
    

    {/*       */}

    <div className="modal fade" id="createNewRecordModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="createNewRecordModalLabel" aria-hidden="true">
		   <div className="modal-dialog">
		    <div className="modal-content">
		      <div className="modal-header">
		        <h5 className="modal-title" id="createNewRecordModalLabel">    </h5>
		        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={handleDropoutFormData}></button>
		      </div>
		      <div className="modal-body modal-scrollable-part">
		        <form method="POST" onSubmit={handleSubmit}>
		          <div className="mb-3">
		            <label for="IMEI_name" className="col-form-label">IMEI:</label>
		            <input type="text" maxLength='15' minLength='15'
                onKeyDown={(event) => {
                  
                  //     
                  //   

                  const whiteListKeys = ['Shift', 'Control', '.', ',', '/', '\\', 'ArrowLeft', 'ArrowRight', 'ArrowUp', 'ArrowDown'];
                  const alphabet = 'abcdefghijklmnopqrstuvwxyz'.split('');

                  if (whiteListKeys.indexOf(event.key) !== -1) {
                    event.preventDefault();

      
                  }

                  if(alphabet.indexOf(event.key) !== -1) {
                    event.preventDefault();

                  }
                  
                }}

                className="form-control" name="IMEI_name" id="IMEI_name" />
		          </div>
		          <div className="mb-3">
		            <label for="Phone_name" className="col-form-label"> :</label>
		            <input type="tel" placeholder="+7(___)___-__-__" className="form-control" name="Phone_name" id="Phone_name" />
		          </div>
		           <div className="mb-3">
		            <label for="CustomerName_name" className="col-form-label">:</label>
		            <input type="text" className="form-control" name="CustomerName_name" id="CustomerName_name" />
		          </div>
		           <div className="mb-3">
		            <label for="RegDate_name" className="col-form-label"> :</label>
		            <input type="date" className="form-control" name="RegDate_name" id="RegDate_name" />
		          </div>

              <div className="mb-3">
                <label for="ServiceType_name" className="col-form-label"> :</label>
                <select className="form-control" name="ServiceType_name" id="ServiceType_name" onChange={(event) => {

                  //      select     useState

                  setNewRecordTariff(tariffData.filter((element) => {return element.id === parseInt(event.target.value, 10)})[0]);
                  
                  }}>

                  {/*        */}

                  {
                    tariffData?.map((tariff, index) => {

                      return <option key={index} value={tariff.id}>{tariff.Title}</option>

                    })
                  }
                </select>
		          </div>


              <div className="mb-3 position-sticky border">         
                  
                  <div className="mx-2">ID: {newRecordTariff.id}</div>
                  <div className="mx-2">: {newRecordTariff.Title}</div>
                  <div className="mx-2"> : {newRecordTariff.Minutes}</div>
                  <div className="mx-2"> : {newRecordTariff.SMS}</div>
                  <div className="mx-2"> : {newRecordTariff.Gigabytes}</div>
                  <div className="mx-2"> (): {newRecordTariff.Price}</div>

		          </div>
              

              <div className="mb-3">
		            <input type="submit" className="form-control btn btn-success" name="submit-btn" id="submit-btn" />
                {/*     */}

                {/* https://stackoverflow.com/questions/46577690/hide-bootstrap-modal-using-pure-javascript-on-click */}

                  {/*       */}
                <button type="button" className="btn-close d-none" data-bs-dismiss="modal" aria-label="Close" id='closeNewRecordModal_btn'></button>
		          </div>

              </form>

            </div>
          </div>
        </div>
        </div>

      {/*         */}





      {/*       */}
      

    <div className="modal fade" id="updateRecordModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="updateRecordModalLabel" aria-hidden="true">
		   <div className="modal-dialog">
		    <div className="modal-content">
		      <div className="modal-header">
		        <h5 className="modal-title" id="updateRecordModalLabel">   </h5>
		        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={handleUpdateDropoutFormData}></button>
		      </div>
		      <div className="modal-body modal-scrollable-part">
		        <form method="POST" onSubmit={handleSimcardUpdate}>

              {/*     value={}      */}

		          <div className="mb-3">
		            <label for="IMEI_update_name" className="col-form-label">IMEI:</label>
		            <input type="text" maxLength='15' minLength='15'
                onKeyDown={(event) => {
                  
                  //     
                  //   

                  const whiteListKeys = ['Shift', 'Control', '.', ',', '/', '\\', 'ArrowLeft', 'ArrowRight', 'ArrowUp', 'ArrowDown'];
                  const alphabet = 'abcdefghijklmnopqrstuvwxyz'.split('');

                  if (whiteListKeys.indexOf(event.key) !== -1) {
                    event.preventDefault();
                  }

                  if(alphabet.indexOf(event.key) !== -1) {
                    event.preventDefault();
                  }
                  
                }}
                
                className="form-control" name="IMEI_update_name" id="IMEI_update_name" />
		          </div>
		          <div className="mb-3">
		            <label for="Phone_update_name" className="col-form-label"> :</label>
		            <input type="tel" placeholder="+7(___)___-__-__" className="form-control" name="Phone_update_name" id="Phone_update_name" />
		          </div>
		           <div className="mb-3">
		            <label for="CustomerName_update_name" className="col-form-label">:</label>
		            <input type="text" className="form-control" name="CustomerName_update_name" id="CustomerName_update_name" />
		          </div>
		           <div className="mb-3">
		            <label for="RegDate_update_name" className="col-form-label"> :</label>
		            <input type="date" className="form-control" name="RegDate_update_name" id="RegDate_update_name" />
		          </div>

              <div className="mb-3">
                <label for="ServiceType_update_name" className="col-form-label"> :</label>
                <select className="form-control" name="ServiceType_update_name" id="ServiceType_update_name" onChange={(event) => {

                  //      select     useState

                  setUpdateRecordTariff(tariffData.filter((element) => {return element.id === parseInt(event.target.value, 10)})[0]);
                  
                  }}>

                  {/*        */}

                  {
                    tariffData?.map((tariff, index) => {

                      return <option key={index} value={tariff.id}>{tariff.Title}</option>

                    })
                  }
                </select>
		          </div>


              <div className="mb-3 position-sticky border">         
                  
                  <div className="mx-2">ID: {updateRecordTariff?.id}</div>
                  <div className="mx-2">: {updateRecordTariff?.Title}</div>
                  <div className="mx-2"> : {updateRecordTariff?.Minutes}</div>
                  <div className="mx-2"> : {updateRecordTariff?.SMS}</div>
                  <div className="mx-2"> : {updateRecordTariff?.Gigabytes}</div>
                  <div className="mx-2"> (): {updateRecordTariff?.Price}</div>

		          </div>
              

              <div className="mb-3">
		            <input type="submit" className="form-control btn btn-success" name="submit-update-btn" id="submit-update-btn" />
                {/*     */}

                {/* https://stackoverflow.com/questions/46577690/hide-bootstrap-modal-using-pure-javascript-on-click */}

                  {/*       */}
                <button type="button" className="btn-close d-none" data-bs-dismiss="modal" aria-label="Close" id='closeUpdateRecordModal_btn'></button>
		          </div>

              </form>

            </div>
          </div>
        </div>
        </div>

        {/*         */}





      

      {/*         */}

      <button type="button" className="btn btn-sm btn-success mx-2" data-bs-toggle="modal" data-bs-target="#createNewRecordModal"><span className="bi-pencil-fill"></span>   </button>
      
      {/*      */}

      <div style={{position: 'absolute', right: '0px', top: '56.5px'}}>
        <span className="mx-2">{username}</span> 
        <button type="button" className="btn btn-sm btn-success mx-2" 
          onClick={handleLogout}
        >
          <span className="bi-box-arrow-left"></span>   </button>
      </div>
      
      {/*      */}
      {/*  ,           */}
      {/*      simcardData    backupSimcardData */}


      <div className="mt-2">
      <input type='text' className="mx-2 form-control" style={{width: '350px'}} placeholder="  " ref={clientNameSearchField} onInput={(event) => {
        // setSearchName      
        // setSearchName(event.target.value.toLowerCase());


        setSimcardData(backupSimcardData.filter(data => {
          return data.ClientName.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1}
        ));
        
        if(event.target.value === '') {
          setSimcardData(backupSimcardData);
        } 

      }}/>

      </div>

      {/*       */}

      {/*       */}

      <div className="modal fade" id="deleteRecordModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="deleteRecordModalLabel" aria-hidden="true">
		  <div className="modal-dialog">
		    <div className="modal-content">
		      <div className="modal-header">
		        <h5 className="modal-title" id="deleteRecordModalLabel">  SIM-</h5>
		        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" ref={closeDeleteModal}></button>
		      </div>
		      <div id="delete-record-modal-body" className="modal-body">
		            {deletableSimcardId}- ?
		      </div>
		      <div class="modal-footer">
		        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal"></button>

          <button type="button" className="btn btn-success" onClick={handleSimcardDelete}></button>

		      </div>
		    </div>
		  </div>
		</div>
    
    {/*          */}


    <table id="simcards-table" className="table-text-align table table-striped mt-2 fw-normal">
		  <thead className="table-header text-black table-success">
		    <tr>
		      <th scope="col">ID</th>
		      <th scope="col">IMEI</th>
		      <th scope="col"> </th>
		      <th scope="col"> </th>
		      <th scope="col"> </th>
		      <th scope="col"> </th>
		      <th scope="col"></th>
		    </tr>
		  </thead>
		  <tbody id="simcard-records">
      {
            simcardData?.map((simcard, index) => {
            return (<tr key={index}>
		      <th scope="row">{ simcard.id }</th>
		      <td>{ simcard.IMEI }</td>
		      <td>{ simcard.PhoneNumber }</td>
		      <td>{ simcard.ClientName }</td>
		      <td>{ simcard.RegistrationDate }</td>
		      <td>{ simcard.TariffPlan.Title }</td>
          <td>

            {/*     */}

            <button type="button" className="btn btn-sm" data-bs-toggle="modal" data-bs-target="#updateRecordModal" onClick={() => {
              setUpdatableSimcardId(simcard.id); 
              
              // alert('simcard.TariffPlan ' + simcard.TariffPlan.Title);
              // tariffData.filter(tariff => parseInt(tariff.id, 10) === parseInt(simcard.TariffPlan.id, 10))[0]

              //         simcardData

              setUpdatableSimcardIndex(simcardData.findIndex(object => object.id === simcard.id));

              //         simcardData

              setUpdatableBackupSimcardIndex(backupSimcardData.findIndex(object => object.id === simcard.id));

              //              
              
              if (simcard.TariffPlan.id === undefined) {

                var tarrif_ = tariffData.filter(tariff => tariff.Title === simcard.TariffPlan.Title)[0];

                document.getElementById("ServiceType_update_name").value = tarrif_.id;
                setUpdateRecordTariff(tarrif_); 

              } else {

                //          value={}       
                document.getElementById("ServiceType_update_name").value = simcard.TariffPlan.id;
                setUpdateRecordTariff(simcard.TariffPlan); 


              }

              //         undefined
              // TariffPlan id  undefined, TariffPlan Title   

              // alert(simcard.TariffPlan.id);
              // alert(simcard.TariffPlan.Title);
              

              document.getElementById("IMEI_update_name").value = simcard.IMEI;
              document.getElementById("Phone_update_name").value = simcard.PhoneNumber;
              document.getElementById("CustomerName_update_name").value = simcard.ClientName;
              document.getElementById("RegDate_update_name").value = simcard.RegistrationDate;


              
              }}>

              <span className="bi-pencil-fill"></span>&nbsp;</button>

            {/*     */}

            <button type="button" class="btn btn-sm" data-bs-toggle="modal" data-bs-target="#deleteRecordModal" onClick={() => {setDeletableSimcardId(simcard.id)}}>
              <span class="bi-eraser-fill"></span>&nbsp;</button>
		      </td>
		      </tr>);

            })
            
            }




		  </tbody>
		</table>


    <table id="simcards-table" className="table-text-align table table-striped mt-2 fw-normal">
		  <thead className="table-header text-black table-success">
          <tr>
		      <th scope="col">ID</th>
		      <th scope="col"></th>
		      <th scope="col"> </th>
		      <th scope="col"> </th>
		      <th scope="col"> </th>
		      <th scope="col"> ()</th>
		    </tr>
		  </thead>
		  <tbody id="simcard-records">
          {
            tariffData?.map((tarrif, index) => {
            return (<tr key={index}>
		      <th scope="row">{ tarrif.id }</th>
		      <td>{ tarrif.Title }</td>
		      <td>{ tarrif.Minutes }</td>
		      <td>{ tarrif.SMS }</td>
		      <td>{ tarrif.Gigabytes }</td>
		      <td>{ tarrif.Price }</td>
		      </tr>);

            })
            
            }
			

		  </tbody>
		</table>

    </div>);

}

export default Base;